import React, { useState, useEffect } from "react";
import ReactCrop from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";

const CroppImg = ({ activePhoto, setActivePhotoCrop }) => {
    const [src, setSrc] = useState(null);
    const [crop, setCrop] = useState({
        aspect: 4 / 1,
    });
    let imageRef = null;

    useEffect(() => {
        setSrc(activePhoto)
        setCrop({
            unit: '%',
            width: 40,
            height: 10,
            x: 25,
            y: 25
        })
    }, [activePhoto]);

    const onImageLoaded = (image) => {
        imageRef = image;
    };

    const onCropComplete = (crop) => {
        makeClientCrop(crop);
    };

    const makeClientCrop = async (crop) => {
        if (imageRef && crop.width && crop.height) {
            const croppedImageUrl = await getCroppedImg(
                imageRef,
                crop,
                'newFile.jpeg'
            );
            setActivePhotoCrop(croppedImageUrl);
        }
    }

    const getCroppedImg = (image, crop, fileName) => {
        const canvas = document.createElement('canvas');
        const pixelRatio = window.devicePixelRatio;
        const scaleX = image.naturalWidth / image.width;
        const scaleY = image.naturalHeight / image.height;
        const ctx = canvas.getContext('2d');

        canvas.width = crop.width * pixelRatio * scaleX;
        canvas.height = crop.height * pixelRatio * scaleY;

        ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
        ctx.imageSmoothingQuality = 'high';

        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            crop.width * scaleX,
            crop.height * scaleY
        );

        return new Promise((resolve, reject) => {
            canvas.toBlob(
                (blob) => {
                    if (!blob) {
                        console.error('Canvas is empty');
                        return;
                    }
                    blob.name = fileName;
                    let fileUrl = window.URL.createObjectURL(blob);
                    resolve(fileUrl);
                },
                'image/jpeg',
                1
            );
        });
    }


    return (
        <div className="cropItem">
            {src && <ReactCrop src={src} crop={crop} onChange={setCrop} onImageLoaded={onImageLoaded} onComplete={onCropComplete} />}
        </div>
    );
};
export default CroppImg