import axios from "axios"

const API_URL = 'https://ibronevik.ru/taxi/api/v1/'

const addToFormData = (formData, object) => {
    for (let [key, value] of Object.entries(object)) {
        if (!value) continue
        formData.append(key, value)
    }
    return formData
}

export const onLogin = (data) => {
    let formData = new FormData()
    addToFormData(formData, { ...data, type: 'e-mail' })
    return axios.post(API_URL + 'auth', formData)
        .then(res => res.data)
        .then(res => {
            if (res.auth_hash) {
                let tokenFormData = new FormData()
                addToFormData(tokenFormData, {
                    auth_hash: res.auth_hash,
                })
                return axios.post(API_URL + 'token', tokenFormData)
                    .then(tokenRes => tokenRes.data)
                    .then(res => {
                        if (res.data) {
                            return res.data
                        } else return res
                    })
            } else return res
        })
        .catch(e => { return { message: 'Network error' } })
}