import React, { useState } from 'react';
import { Panel, PanelHeader, Button, Group, FormItem, Input, Div, FormLayout } from '@vkontakte/vkui';
import * as api from '../api/api';
import { useRouter } from '@happysanta/router';
import { MODAL_ERROR, PAGE_MAIN } from '../routers';

const Auth = ({ id, setModalErrorText }) => {
	const [login, setLogin] = useState('')
	const [password, setPassword] = useState('')

	const router = useRouter();

	const onLogin = async () => {
		const auth_data = await api.onLogin({ login, password })
		if (auth_data.message) {
			setModalErrorText(auth_data.message)
			router.pushModal(MODAL_ERROR)
		} else {
			localStorage.setItem('token', auth_data.token)
			localStorage.setItem('u_hash', auth_data.u_hash)
			router.replacePage(PAGE_MAIN)
		}
	}
	return <Panel id={id}>
		<PanelHeader>Авторизация</PanelHeader>
		<Group>
			<FormLayout>
				<FormItem top='Логин'>
					<Input placeholder='Введите логин' value={login} onChange={(e) => setLogin(e.target.value)} />
				</FormItem>
				<FormItem top='Пароль'>
					<Input placeholder='Введите пароль' type='password' value={password} onChange={(e) => setPassword(e.target.value)} />
				</FormItem>
				<Div>
					<Button type='submit' stretched size='l' onClick={onLogin} disabled={login.length === 0 || password.length === 0}>Войти</Button>
				</Div>
			</FormLayout>
		</Group>
	</Panel>
}

export default Auth;
