import '@vkontakte/vkui/dist/vkui.css';
import React, { useState, useEffect } from 'react';
import { ModalRoot, SplitLayout, SplitCol, View, AdaptivityProvider, AppRoot, ConfigProvider, WebviewType } from '@vkontakte/vkui';
import Home from './panels/Home';
import { MODAL_ERROR, PAGE_AUTH, PANEL_AUTH, PANEL_MAIN, VIEW_MAIN } from './routers';
import { useLocation, useRouter } from '@happysanta/router';
import './panels/style.css'
import Auth from './panels/Auth';
import { CardError } from './modals/CardError';
function App() {
	const location = useLocation();
	const router = useRouter();

	const [modalErrorText, setModalErrorText] = useState('')

	const modal = <ModalRoot activeModal={location.getModalId()}
		onClose={() => router.popPage()}
	>
		<CardError id={MODAL_ERROR} onClose={() => router.popPage()} modalErrorText={modalErrorText} />
	</ModalRoot>;

	useEffect(() => {
		if (!localStorage.getItem('token') && !localStorage.getItem('u_hash')) {
			router.replacePage(PAGE_AUTH)
		}
	}, [router]);


	return <ConfigProvider webviewType={WebviewType.INTERNAL}>
		<AdaptivityProvider>
			<AppRoot>
				<SplitLayout modal={modal} style={{ justifyContent: "center" }}>
					<SplitCol maxWidth={800} spaced={window.innerWidth > 800}>
						<View id={VIEW_MAIN}
							onSwipeBack={() => router.popPage()}
							history={location.hasOverlay() ? [] : location.getViewHistory(VIEW_MAIN)}
							activePanel={location.getViewActivePanel(VIEW_MAIN)}
						>
							<Home id={PANEL_MAIN} />
							<Auth id={PANEL_AUTH} setModalErrorText={setModalErrorText} />
						</View>

					</SplitCol>
				</SplitLayout>
			</AppRoot>
		</AdaptivityProvider>
	</ConfigProvider>

}

export default App;


