import React, { useState, useEffect } from 'react';
import { Panel, PanelHeader, Button, Group, Tabs, TabsItem, Cell, FormItem, File, CellButton, Input, Switch, HorizontalScroll, Select, Checkbox, Div, PanelHeaderButton } from '@vkontakte/vkui';

import { Icon24Camera, Icon24ChevronDown, Icon24ChevronUp, Icon28HelpCircleOutline } from '@vkontakte/icons';
import AnimateHeight from 'react-animate-height';
import CropImg from '../components/CropImg'

let data = [{
	id: 1,
	city: "Воронеж",
	street: "Чернышевского",
	house: "22",
	apartment: "1",
}, {
	id: 2,
	city: "Воронеж",
	street: "Чернышевского",
	house: "22",
	apartment: "2",
}, {
	id: 3,
	city: "Воронеж",
	street: "Чернышевского",
	house: "22",
	apartment: "3",
}, {
	id: 4,
	city: "Воронеж",
	street: "Победы",
	house: "2",
	apartment: "2",
}, {
	id: 5,
	city: "Воронеж",
	street: "Победы",
	house: "2",
	apartment: "3",
}, {
	id: 5,
	city: "Воронеж",
	street: "Победы",
	house: "3",
	apartment: "3",
}]

const Home = ({ id }) => {
	const [additionalFields, setAdditionalFields] = useState(false)
	const [addssFields, setAddssFields] = useState(true)
	const [previewFields, setPreviewFields] = useState(true)

	const [cityOptions, setCityOptions] = useState([])
	const [streetOptions, setStreetOptions] = useState([])
	const [houseOptions, setHouseOptions] = useState([])
	const [apartmentOptions, setApartmentOptions] = useState([])

	const [cityValue, setCityValue] = useState('')
	const [streetValue, setStreetValue] = useState('')
	const [houseValue, setHouseValue] = useState('')
	const [apartmentValue, setApartmentValue] = useState('')

	const [activePhoto, setActivePhoto] = useState(null)
	const [activePhotoCrop, setActivePhotoCrop] = useState(null)

	useEffect(() => {
		let cityVariables = [...new Set(data.map(x => x.city))]
		setCityOptions(cityVariables.map(x => { return { label: x, value: x } }))
	}, []);


	const changeFile = (e) => {
		console.log(e.target.files)
		if (e.target.files && e.target.files.length > 0) {
			const reader = new FileReader();
			reader.addEventListener('load', () =>
				setActivePhoto(reader.result)
			);
			reader.readAsDataURL(e.target.files[0]);
		}
	}

	const toggleCity = (e) => {
		let city = e.target.value
		let streetsOfCity = data.filter(x => x.city === city)
		let streetVariables = [...new Set(streetsOfCity.map(x => x.street))]
		setStreetOptions(streetVariables.map(x => { return { label: x, value: x } }))
		setCityValue(city)
		setStreetValue('')
		setHouseValue('')
		setApartmentValue('')
	}

	const toggleStreet = (e) => {
		let street = e.target.value
		let housesOfCity = data.filter(x => x.city === cityValue && x.street === street)
		let housesVariables = [...new Set(housesOfCity.map(x => x.house))]
		setHouseOptions(housesVariables)
		setStreetValue(street)
		setHouseValue('')
		setApartmentValue('')
	}

	const toggleHouse = (house) => {
		let apartmentsOfCity = data.filter(x => x.city === cityValue && x.street === streetValue && x.house === house)
		let apartmentsVariables = [...new Set(apartmentsOfCity.map(x => x.apartment))]
		setApartmentOptions(apartmentsVariables)
		setHouseValue(house)
		setApartmentValue('')
	}

	const toggleApartment = (apartment) => {
		console.log(apartment)
		setApartmentValue(apartment)
		// document.getElementById('btnPhoto').click()
	}

	return <Panel id={id}>
		<PanelHeader
			left={<PanelHeaderButton><Icon28HelpCircleOutline /></PanelHeaderButton>}
		>
			Съём
		</PanelHeader>
		<Group>
			<CellButton after={addssFields ? <Icon24ChevronUp /> : <Icon24ChevronDown />} onClick={() => setAddssFields(!addssFields)}>Локатор</CellButton>
			<AnimateHeight height={addssFields ? 'auto' : 0}>
				{/* <FormItem top="Город">
					<Selectect
						className='CustomSelectectAsVk'
						classNamePrefix="react-select"
						options={cityOptions}
						placeholder='Выберите город'
					/>
				</FormItem> */}
				<FormItem top="Город">
					<Select
						placeholder="Выберите город"
						searchable='true'
						options={cityOptions}
						value={cityValue}
						onChange={toggleCity}
					/>
				</FormItem>
				<FormItem top="Улица">
					<Select
						disabled={cityValue === ''}
						placeholder="Выберите улицу"
						searchable='true'
						options={streetOptions}
						value={streetValue}
						onChange={toggleStreet}
					/>
				</FormItem>
				{streetValue !== '' && <FormItem top="Дом">
					<HorizontalScroll
						showArrows
						getScrollToLeft={(i) => i - 120}
						getScrollToRight={(i) => i + 120}
					>
						<Tabs mode="buttons" style={{ display: "flex" }}>
							{houseOptions.map(i => <div className='mv4' key={i}>
								<TabsItem
									selected={i === houseValue}
									onClick={() => toggleHouse(i)}
								>
									{i}
								</TabsItem>
							</div>)}
						</Tabs>
					</HorizontalScroll>
				</FormItem>}
			</AnimateHeight>
			{houseValue !== '' && <FormItem top="Квартира">
				<HorizontalScroll
					showArrows
					getScrollToLeft={(i) => i - 120}
					getScrollToRight={(i) => i + 120}
				>
					<Tabs mode="buttons" style={{ display: "flex" }}>
						{apartmentOptions.map(i => <div className='mv4' key={i}>
							<TabsItem
								selected={i === apartmentValue}
								onClick={() => toggleApartment(i)}
							>
								{i}
							</TabsItem>
						</div>)}
					</Tabs>
				</HorizontalScroll>
			</FormItem>}

			<Div style={{ display: 'flex' }}>
				<File id='btnPhoto' before={<Icon24Camera />} controlSize="l" stretched accept="image/*;capture=camera" style={{ marginRight: 8 }} onChange={changeFile}>
					Сделать фото
				</File>
				<Button className='btnText' before={<Icon24Camera />} size="l" mode="secondary" stretched disabled>Просмотр фото</Button>
			</Div>

			<CropImg activePhoto={activePhoto} setActivePhotoCrop={setActivePhotoCrop} />

			<FormItem top='Показание'>
				<Input placeholder='Введите показание' />
			</FormItem>
			<FormItem>
				<Button size='l' stretched>Отправить</Button>
			</FormItem>
			<FormItem>
				<Checkbox>Автоматический переход к следующей квартире</Checkbox>
				<Checkbox checked={previewFields} onChange={e => setPreviewFields(e.target.checked)}>Предпросмотр фото</Checkbox>
			</FormItem>

			<CellButton after={additionalFields ? <Icon24ChevronUp /> : <Icon24ChevronDown />} onClick={() => setAdditionalFields(!additionalFields)}>Дополнительные поля</CellButton>
			<AnimateHeight height={additionalFields ? 'auto' : 0}>
				<FormItem top='Тип ПУ'>
					<Input placeholder='Введите тип ПУ' />
				</FormItem>
				<FormItem top='Номер ПУ'>
					<Input placeholder='Введите номер ПУ' />
				</FormItem>
				<FormItem top="Год поверки">
					<Select
						placeholder="Выберите год поверки"
						searchable='true'
						options={[...Array(23).keys()].map(i => {
							return { label: (2000 + i).toString(), value: (2000 + i).toString() }
						})}
					/>
				</FormItem>
				<FormItem top="Класс точности">
					<Select
						placeholder="Выберите класс точности"
						searchable='true'
						options={[{
							label: "1.0",
							value: "1.0",
						},
						{
							label: "2.0",
							value: "2.0",
						},
						{
							label: "2.5",
							value: "2.5",
						},
						{
							label: "5.0",
							value: "5.0",
						}]}
					/>
				</FormItem>
				<FormItem top="Значность">
					<Select
						placeholder="Выберите значность"
						searchable='true'
						options={[{
							label: "1",
							value: "1",
						}, {
							label: "4",
							value: "4",
						},
						{
							label: "5",
							value: "5",
						},
						{
							label: "6",
							value: "6",
						},
						{
							label: "7",
							value: "7",
						}]}
					/>
				</FormItem>
				<Cell
					disabled
					after={<Switch aria-label="Пломба поверки" />}
				>
					Пломба поверки
				</Cell>
				<Cell
					disabled
					after={<Switch aria-label="Пломба ЭСО" />}
				>
					Пломба ЭСО
				</Cell>
				<Cell
					disabled
					after={<Switch aria-label="Антимагнитная" />}
				>
					Антимагнитная
				</Cell>
				<FormItem top='Дата'>
					<Input placeholder='Введите дату' value={new Date().toLocaleDateString('ru-RU')} onChange={() => { }} />
				</FormItem>
				<FormItem top='Место установки'>
					<Input placeholder='Введите место установки' />
				</FormItem>
				<FormItem top='Примечание'>
					<Input placeholder='Введите примечание' />
				</FormItem>
				<FormItem>
					<Button size='l' stretched>Отправить</Button>
				</FormItem>
			</AnimateHeight>
		</Group>
	</Panel>
}

export default Home;
